import React from 'react'
import { Modal, Header, Grid, Image } from 'semantic-ui-react'
import { getImageAddress } from './../../../module/globals'

export default class extends React.Component {

    render() {
        const { titulo, imagens, children } = this.props
        return (
            <Modal
                basic
                trigger={children}>
                <Header icon='image' content={titulo || 'Imagens'} />
                <Modal.Content>
                    {imagens.map((im, i) => (
                        <Image fluid src={getImageAddress(im)} key={i} />
                    ))}
                </Modal.Content>
            </Modal>
        )
    }

}


